type SolvedProblemProps = {
  problemName: string;
  problem: React.ReactElement;
  importance?: React.ReactElement;
  solution: React.ReactElement;
  solutionImage?: React.ReactElement;
  problemImage?: React.ReactElement;
  telgorithmSolution?: boolean;
  titleColor?: string;
  wideSolutionBlock?: boolean;
};

export const SolvedProblem: FC<SolvedProblemProps> = ({
  problemName,
  problem,
  importance,
  solution,
  solutionImage,
  telgorithmSolution,
  problemImage,
  titleColor,
  wideSolutionBlock
}) => {
  return (
    <SolvedProblemContainer titleColor={titleColor}>
      <Text.Title.Bold.md>{problemName}</Text.Title.Bold.md>
      <div>
        <SolvedProblemBox withImage={Boolean(problemImage)}>
          <SolvedProblemContentBlock>
            <CircleQuestionIcon />
            <Text.Regular.Bold.lg>The Problem</Text.Regular.Bold.lg>
            <div>{problem}</div>
          </SolvedProblemContentBlock>
          {importance && (
            <SolvedProblemContentBlock>
              <ShieldWarningIcon />
              <Text.Regular.Bold.lg>Why it Matters</Text.Regular.Bold.lg>
              <div>{importance}</div>
            </SolvedProblemContentBlock>
          )}
          {problemImage && <ProblemImage>{problemImage}</ProblemImage>}
        </SolvedProblemBox>
        <SolvedProblemBox withImage={Boolean(solutionImage)} wideSolutionBlock={wideSolutionBlock}>
          <SolvedProblemContentBlock>
            <BadgeSuccessIcon />
            <Text.Regular.Bold.lg>
              {telgorithmSolution ? 'Telgorithm’s Solution' : 'The Solution'}
            </Text.Regular.Bold.lg>
            <div>{solution}</div>
          </SolvedProblemContentBlock>
          {solutionImage && <SolutionImage>{solutionImage}</SolutionImage>}
        </SolvedProblemBox>
      </div>
    </SolvedProblemContainer>
  );
};

